body{margin:0;font-family:'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:'Courier New', monospace}*{box-sizing:border-box}.search-form{max-width:600px;background:#4285f4;padding:15px 20px;margin:20px auto}.search-form__inputs{display:flex;justify-content:space-between}.search-form__inputs input{width:69%;border:0;padding:5px;outline:none}.search-form__inputs select{width:29%;border:0;outline:none}.search-form__action{margin-top:10px}.search-form__action button{background-color:#fbbc05;display:block;width:100px;padding:5px 10px;margin:0 auto;border:0;outline:none}@media screen and (max-width: 1024px){.search-form{max-width:90%}}.search-results{max-width:600px;margin:0 auto}.search-results__title{color:#444}@media screen and (max-width: 1024px){.search-results{max-width:90%}}.search-results__item a{text-decoration:none}.search-results__item a p{margin:0 0 20px 0;color:#333}.search-results__item a .display-link{font-size:10px;display:block;color:#333;font-style:italic}.search-results__item a:hover .title-link{text-decoration:underline}
